<template>
  <div class="detailed-view-container">
    <h2>{{project.name}}</h2>

    <p>{{project.description}}</p>

    <div class="view" :style="backgroundImageStyle" @click="showModal = true" ref="projectView">
    </div>

    <div class="tags">
      <div v-for="tag in project.tags.front" :key="tag" class="tag front">
        <p>{{tag}}</p>
      </div>

      <div v-for="tag in project.tags.back" :key="tag" class="tag back">
        <p>{{tag}}</p>
      </div>
    </div>

    <!-- Modal permettant d'afficher en grand l'image d'un projet -->
    <div v-if="showModal" class="modal" @click="showModal = false">
      <div class="modal-content">
        <img :src="project.image" alt="Image agrandie"/>
      </div>
    </div>

  </div>
</template>

<script>
export default {
  name: "DetailedView",
  props: {
    project: {
      required: true
    }
  },
  data() {
    return {
      imageHeight: 280, // Hauteur initiale
      showModal: false, // Pour afficher ou masquer la modale
    }
  },
  computed: {
    backgroundImageStyle() {
      // Vérifiez si project.image est défini
      if (this.project.image) {
        // Utilisez l'URL de l'image comme fond
        return {
          backgroundImage: `url('${this.project.image}')`,
          height: `${this.imageHeight}px`,
        };
      } else {
        // Aucune image n'est définie, utilisez un fond par défaut ou laissez-le vide
        return {};
      }
    },
  },
  mounted() {
    this.calculateBackgroundImageStyle();
  },
  methods: {
    calculateBackgroundImageStyle() {
      if (this.project.image) {
        const img = new Image();
        img.src = this.project.image;

        img.onload = () => {
          const imgWidth = img.width;
          const imgHeight = img.height;
          const imgRatio = imgWidth / imgHeight;

          // Met la variable à jour
          const viewElement = this.$refs.projectView;

          this.imageHeight = viewElement.offsetWidth - (Math.abs(1 - imgRatio) * 85);
        };
      }
    },
  },
}
</script>

<style scoped>
.detailed-view-container {
  display: flex;
  flex-direction: column;

  flex: 1;

  gap: 8px;

  scroll-margin-top: 10px; /* Pour mettre le scroll au début du composant lors du changement de tabulation */
}

.detailed-view-container p{
  font-size: 16px;
}

.view {
  height: 280px;

  display: flex;
  flex-direction: column;

  border-radius: 5px;

  cursor: pointer;

  background-size: contain;
  background-position: center;
  background-repeat: no-repeat;
}

.tags{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;

  gap: 6px;
}

.tag{
  display: flex;
  flex-direction: column;

  padding: 4px 8px;
  border-radius: 5px;
}

.tag p{
  color: var(--white);

  font-weight: bold;
}

.front{
  background-color: var(--front-color);
}

.back{
  background-color: var(--back-color);
}

/* Modal styles */
.modal {
  width: 100%;
  height: 100%;

  position: fixed;

  display: flex;
  justify-content: center;
  align-items: center;

  top: 0;
  left: 0;
  z-index: 2;

  cursor: pointer;

  background-color: var(--dark-shadow);
}

.modal-content img{
  max-width: 90%;
  max-height: 90vh;
  display: block;
  margin: 0 auto;
}

@media screen and (max-height: 780px) {
  .detailed-view-container p{
    font-size: 14px;
  }

  .tag p{
    font-size: 12px;
  }
}


</style>
